import React, { useEffect, useState } from 'react';
import { makeStyles, useTheme } from '@material-ui/styles';
import { Grid, Box } from '@material-ui/core';

const useStyles = makeStyles(theme => ({
  main_logo: {
    height: 40
  },
  description: {
    fontSize: 52,
    color: 'white',
    lineHeight: '60px',
    fontWeight: 500,
    maxWidth: 382,
    marginTop: 64
  }
}));

const Auth = props => {
  const { children } = props;

  const classes = useStyles();

  return (
    <Grid container>
      <Grid item xs={12} sm={6}>
        <Box style={{padding: 40, backgroundColor: 'blue', height: '100vh'}}>
          <img src="/images/Logo.svg" className={classes.main_logo}/>
          <Box className={classes.description}>
            Let's get you set up with a Clutch Wallet profile!
          </Box>
        </Box>
      </Grid>
      <Grid item xs={12} sm={6}>
        {children}
      </Grid>
    </Grid>
  );
};

export default Auth;
